	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨断断续续地做了几个梦，基本内容是：</p>\n<p>\"我到一个地方去找鱼，那里的人说不行，结果我用望远镜看。看到一辆三轮车上面有鱼，鱼下面是一种什么蔬菜。到了一个广场，广场上有很多的人，在表演各个年代的服装，从五十年代到现在。后来就游行，我们排在队伍的前面，有很多人拍照，盯住我们拍。有一女的在指挥，一个人看三个，四个人八只眼什么的到了一间办公室，里面有一张桌子，我看是旧桌子，就检查抽屉，扔掉一些没有用的纸头。右手边一个抽屉，打开以后有很多的钥匙，都用小皮夹装着，大概有十几个整整齐齐。在这一边，有一摞书，看了一两本，差点倒下来。发现一个非常小的电脑打字机，非常惊奇。桌上有一女的照片中午吃饭，一个年轻人进来吃饭，好像桌子是他的，我没理他。他有点外国人的样子，头发颓了一些，鼻子很尖。我在旁边看书，想到隔壁的房间里休息，也没有去。他在和一人讲话，好象是熟人。他说自己刚离婚，那人说是哪一个，叫来看看到了一个大的建筑物，在门口检查了证件放进去，好象是留学生楼。里面有很多外国学生，也有中国学生。遇到许多熟人，打招呼。一名外国学生在门上引体向上，大家还比。然后向前走，很多的人向前走，好象是去食堂\"</p>\n<p>这个梦是什么意思?昨天晚上电视里放过一个节目，有过去服装的展示;昨天上午一朋友拿一手提式电脑给我看。其他就没有头绪了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0135.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0135.md","frontmatter":{"title":"8月16日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0135.md absPath of file >>> MarkdownRemark"}}

